<template>

    <div class="img-back" style="position: relative; height: 100%!important;">
        <v-container class="login" style="position: relative; height: 100%; max-width: 550px">
            <v-row class="justify-center text-center">
                <v-card-title class="text-center justify-center pa-3 white--text">
                    {{verifyYourEmailText}}
                </v-card-title>

                <v-col cols="12" style="padding-bottom: 0px">
                    <v-card style="padding-bottom: 8px" class="text--center justify-center">
                        <v-container>
                            <v-row class="pa-3">
                                <v-col text-align="center">
                                    <v-card-text class="pa-0" align="justify">
                                        {{verificationEmailSentTo}}
                                    </v-card-text>
                                    <v-card-text class="pa-0" align="center">
                                        <b>{{getEmail}}</b>
                                    </v-card-text>
                                </v-col>
                            </v-row>

                            <v-row class="pa-3">
                                <v-col align="center">
                                    <v-progress-circular
                                            size="100"
                                            color="primary"
                                            indeterminate
                                    >
                                        <img style="width: 60px" src="../../assets/EmailLogoWeBabble.png"/>
                                    </v-progress-circular>
                                    <br>
                                    <br>
                                </v-col>
                            </v-row>

                            <v-btn block color="primary" @click="sendUserEmailVerification" style="margin-top: 10px">
                                {{resendEmailText}}
                            </v-btn>
                        </v-container>
                    </v-card>
                </v-col>
            </v-row>
            <v-row style="position: fixed; bottom: 10px; width: 100%; left: 0px">
                <v-col></v-col>
                <v-col align="center" class="pa-0">
                    <a @click="wantToLogout" class="white--text" style="font-size: 14px">
                        <u>{{logOutText}}</u>
                    </a>
                </v-col>
                <v-col></v-col>
            </v-row>

            <v-dialog
                    v-model="dialog"
                    max-width="500px"
            >
                <v-card>
                    <br>
                    <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                        <h3 style="margin-bottom: 10px">{{aboutToLogOutText}}</h3>
                        <p style="margin-bottom: 7px">{{askingForLogOutConfirmationText}}</p>
                    </v-card-text>

                    <v-card-actions style="padding-bottom: 15px">
                        <v-spacer></v-spacer>
                        <v-btn color="primary" @click="dialog = false">{{cancelText}}</v-btn>
                        <v-btn color="primary" @click="logoutUser">{{confirmText}}</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="primary"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
                </v-btn>
            </v-snackbar>
        </v-container>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';

    // Página de registro verificación de E-mail
    // @group Cuenta
    export default {
        name: "EmailVerification",
        data() {
            return {
                dialog: false,
                sendEmailButtonIsDisabled: true,
                snackbar: false,
                snackbarText: '',
                isEmailVerified: false,
                intervalCheckEmailVerified: null,
                logOutText: null,
                resendEmailText: null,
                closeText: null,
                cancelText: null,
                toLogOutText: null,
                confirmText: null,
                aboutToLogOutText: null,
                verificationEmailSentTo: null,
                askingForLogOutConfirmationText: null,
                verifyYourEmailText: null,
                verificationEmailSentText: null
            }
        },
        methods: {
            ...mapActions('user', ['logout', 'sendEmailVerification', 'checkIfEmailIsVerified', 'checkRegistrationState']),
            // @vuese
            // Envía un E-mail de verificación al usuario
            sendUserEmailVerification: function () {
                //Send verification email
                let self = this;
                let resultSendEmailVerification = this.sendEmailVerification();

                resultSendEmailVerification.then(() => {
                    self.snackbarText = self.verificationEmailSentText;
                    self.snackbar = true;
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },

            // @vuese
            // Abre un diálogo advirtiendo al usuario pidiéndole confirmar que desea cerrar sesión
            wantToLogout() {
                let self = this;
                self.dialog = true;
            },

            // @vuese
            // Para la ejecución periódica de intervalCheckEmailVerified y cierra la sesión del usuario
            logoutUser() {
                let resultLogout = this.logout();

                resultLogout.then(() => {
                    clearInterval(this.intervalCheckEmailVerified);
                    this.$router.replace('home');
                })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },

            // @vuese
            // Comprueba si el usuario ha verificado su E-mail y si lo ha hecho, actualiza su estado y lo redirige al siguiente paso del proceso de registro
            checkEmailVerified: function () {
                let self = this;
                let resultCheckIfEmailIsVerified = this.checkIfEmailIsVerified();

                resultCheckIfEmailIsVerified
                    .then((isVerified) => {
                        if (isVerified) {
                            clearInterval(self.intervalCheckEmailVerified);

                            let resultCheckRegistrationState = self.checkRegistrationState(self.$store.getters['user/getProfile']);

                            resultCheckRegistrationState
                                .then((goal) => {
                                    self.$router.replace(goal);
                                })
                                .catch((error) => {
                                    self.snackbarText = error;
                                    self.snackbar = true;
                                });
                        }
                    })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });

            }
        },

        mounted() {
            this.sendEmailVerification().catch((error) => {
                if (error != this.$i18n.t('messages.ERROR_TOO_MANY_REQUESTS')) {
                    self.snackbarText = error;
                    self.snackbar = true;
                }
            });
            this.intervalCheckEmailVerified = setInterval(this.checkEmailVerified, 2000);
        },

        created() {
            this.logOutText = this.$i18n.t('messages.TEXT_TO_LOG_OUT');
            this.resendEmailText = this.$i18n.t('messages.TEXT_RESEND_EMAIL');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.cancelText = this.$i18n.t('messages.TEXT_CANCEL');
            this.toLogOutText = this.$i18n.t('messages.TEXT_TO_LOG_OUT');
            this.confirmText = this.$i18n.t('messages.TEXT_CONFIRM');
            this.aboutToLogOutText = this.$i18n.t('messages.TEXT_YOU_ARE_ABOUT_TO_LOG_OUT');
            this.verificationEmailSentTo = this.$i18n.t('messages.TEXT_VERIFICATION_EMAIL_SENT_TO');
            this.askingForLogOutConfirmationText = this.$i18n.t('messages.TEXT_ARE_YOU_SURE_YOU_WANT_TO_LOG_OUT');
            this.verifyYourEmailText = this.$i18n.t('messages.TEXT_VERIFY_YOUR_EMAIL');
            this.verificationEmailSentText = this.$i18n.t('messages.TEXT_VERIFICATION_EMAIL_SENT');
        },

        computed: {
            ...mapGetters('user', ['getEmail'])
        }
    }
</script>

<style scoped>

</style>
